import {
	mapState,
	mapMutations
} from 'vuex'
import fish from "@/components/xiaodiu-fish-h5/xiaodiu-fish.vue"
import fishWx from "@/components/xiaodiu-fish-wx/xiaodiu-fish.vue"
export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	components: {fish,fishWx},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		bug() {
			 this.$refs.fishWx.addfish(1)
		}
		
	}
}
